import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Watcher from '@/components/Watcher'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/watcher',
      name: 'Watcher',
      component: Watcher
    }
  ]
})



